html,body {
  /* font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI',
      Roboto, 'Helvetica Neue', Arial, sans-serif; */
  font-size: 16px;
      word-spacing: 1px;
      -ms-text-size-adjust: 100%;
      -webkit-text-size-adjust: 100%;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      box-sizing: border-box;
      color: #2E3440;
      font-family: 'Quicksand', 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  }
  
  *,
  *:before,
  *:after {
      box-sizing: border-box;
      margin: 0;
  }
  
  .button--green, input[type="submit"] {
      display: inline-block;
      border-radius: 4px;
      border: 1px solid #3b8070;
      color: #3b8070;
      text-decoration: none;
  
      padding-top: .75rem;
      padding-bottom: .75rem;
      padding-left: 1rem;
      padding-right: 1rem;
  }
  
  .button--green:hover {
      color: #fff;
      background-color: #3b8070;
  }
  
  .button--grey {
      display: inline-block;
      border-radius: 4px;
      border: 1px solid #35495e;
      color: #35495e;
      text-decoration: none;
      padding: 10px 30px;
      margin-left: 15px;
  
      
      padding-top: .75rem;
      padding-bottom: .75rem;
      padding-left: 1rem;
      padding-right: 1rem;
  
  }
  
  .button--grey:hover {
  color: #fff;
  background-color: #35495e;
  }
  
  .container {
      margin: 0 auto;
      min-height: 100vh;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
  }
  
  .title {
  
      display: block;
      font-weight: 300;
      font-size: 100px;
      letter-spacing: 1px;
  }
  
  .subtitle {
      font-weight: 300;
      font-size: 42px;
      color: #434C5E;
      word-spacing: 5px;
      padding-bottom: 15px;
  }  
  
  input {
      border: solid;
      border-width: 1px;
      padding-left: 1rem;
      padding-right: 1rem;
      padding-top: .75rem;
      padding-bottom: .75rem;
      border-radius: 4px;
      border-color:#4C566A;
  }
  
  /* Navbar */
  .navbar {
      display: flex;
      flex-direction: row;
      justify-content: start;
      position: fixed;
      width: 100%;
      top:0;
      left:0;
  }
  
  .navbar h1 {
      font-weight: 300;
      color: #35495e;
      letter-spacing: 1px;
      height: 80px;
      align-items: center;
      text-align: center;
      text-overflow: clip;
  }
  
  .navbar > * {
      margin: 0;
      padding: 16px;
      padding-right: 0;
      height: 80px;
  }
  
  .home {
      margin: 0;
      padding: 16px;
      padding-right: 0;
  }
  
  .navbar a {
      text-decoration: none;
      display: flex;
      flex-direction: row;
  }
  
  .navbar .logo {
      margin: 0;
      width: 48px;
  }
  
  
  /* forms */
  
  
  form {
      display: grid;
  }
  
  form input {
      margin: 10px;
  }
  
  
  /* Page animations */
  
  .page-enter-active, .page-leave-active {
      transition: opacity 125ms
    }
    .page-enter, .page-leave-active {
      opacity: 0
    }


    .page--unprotected {
      margin: 0 auto;
      margin-top: 80px;
      /* min-height: 100vh; */
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
  }